"use client";

import React from "react";
import Link from "next/link";
import { motion } from "framer-motion";
import { Users, Clock, BookOpen, TrendingUp, Star } from "lucide-react";
import { CourseThumbnail } from "@/components/ui/CourseThumbnail";

export interface PublicCourse {
    id: string;
    title: string;
    description: string | null;
    category: string | null;
    price: number;
    thumbnail_url: string | null;
    is_published: boolean;
    created_at: string;
    creator_id: string;
    profiles?: {
        full_name: string;
    };
}

export const CourseCard: React.FC<{ course: PublicCourse }> = ({ course }) => {
    const categoryConfig = {
        'University': {
            color: 'from-blue-500 to-blue-600',
            bgColor: 'bg-blue-50',
            textColor: 'text-blue-700',
            icon: 'ðŸŽ“'
        },
        'Beginner': {
            color: 'from-teal-500 to-teal-600',
            bgColor: 'bg-teal-50',
            textColor: 'text-teal-700',
            icon: 'ðŸŒ±'
        },
        'High School': {
            color: 'from-orange-500 to-orange-600',
            bgColor: 'bg-orange-50',
            textColor: 'text-orange-700',
            icon: 'ðŸ“š'
        },
        'Advanced': {
            color: 'from-purple-500 to-purple-600',
            bgColor: 'bg-purple-50',
            textColor: 'text-purple-700',
            icon: 'ðŸš€'
        }
    };

    const config = categoryConfig[course.category as keyof typeof categoryConfig] || {
        color: 'from-gray-500 to-gray-600',
        bgColor: 'bg-gray-50',
        textColor: 'text-gray-700',
        icon: 'ðŸ“–'
    };

    return (
        <Link href={`/courses/${course.id}`} className="block h-full">
            <motion.div
                whileHover={{ y: -8, scale: 1.02 }}
                transition={{ duration: 0.3, ease: "easeOut" }}
                className="group relative bg-white rounded-3xl shadow-lg hover:shadow-2xl border border-gray-100 overflow-hidden h-full flex flex-col cursor-pointer min-w-[300px] md:min-w-0"
            >
                {/* Thumbnail Section */}
                <div className="relative h-48 overflow-hidden bg-gradient-to-br from-gray-100 to-gray-200">
                    <CourseThumbnail
                        src={course.thumbnail_url}
                        title={course.title}
                        category={course.category || "Course"}
                        className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-500"
                    />

                    {/* Gradient Overlay */}
                    <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-black/20 to-transparent" />

                    {/* Category Badge */}
                    <div className="absolute top-4 left-4">
                        <div className={`flex items-center gap-2 px-3 py-1.5 ${config.bgColor} backdrop-blur-sm rounded-full border border-white/20 shadow-lg`}>
                            <span className="text-sm">{config.icon}</span>
                            <span className={`text-xs font-bold ${config.textColor}`}>
                                {course.category || 'Course'}
                            </span>
                        </div>
                    </div>

                    {/* Rating Badge */}
                    <div className="absolute top-4 right-4">
                        <div className="flex items-center gap-1 px-3 py-1.5 bg-white/90 backdrop-blur-sm rounded-full shadow-lg">
                            <Star className="w-3.5 h-3.5 fill-yellow-400 text-yellow-400" />
                            <span className="text-xs font-bold text-gray-900">4.8</span>
                        </div>
                    </div>
                </div>

                {/* Content Section */}
                <div className="flex-1 p-6 flex flex-col">
                    {/* Title & Description */}
                    <h3 className="text-xl font-bold text-gray-900 mb-3 line-clamp-2 group-hover:text-[#1F2A6B] transition-colors">
                        {course.title}
                    </h3>
                    <p className="text-sm text-slate-600 mb-4 line-clamp-2 flex-1">
                        {course.description || "Comprehensive course designed to help you master the concepts."}
                    </p>

                    {/* Course Stats */}
                    <div className="grid grid-cols-2 gap-3 mb-4 pb-4 border-b border-gray-100">
                        <div className="flex items-center gap-2 text-xs text-slate-600">
                            <div className="w-8 h-8 rounded-lg bg-blue-50 flex items-center justify-center">
                                <BookOpen className="w-4 h-4 text-blue-600" />
                            </div>
                            <div>
                                <div className="font-semibold text-gray-900">24+</div>
                                <div className="text-[10px]">Lessons</div>
                            </div>
                        </div>
                        <div className="flex items-center gap-2 text-xs text-slate-600">
                            <div className="w-8 h-8 rounded-lg bg-teal-50 flex items-center justify-center">
                                <Clock className="w-4 h-4 text-teal-600" />
                            </div>
                            <div>
                                <div className="font-semibold text-gray-900">12h</div>
                                <div className="text-[10px]">Duration</div>
                            </div>
                        </div>
                    </div>

                    {/* Footer */}
                    <div className="flex justify-between items-center">
                        <div className="flex items-center gap-2">
                            <div className="w-8 h-8 rounded-full bg-gradient-to-br from-gray-200 to-gray-300 flex items-center justify-center">
                                <Users className="w-4 h-4 text-gray-600" />
                            </div>
                            <div className="text-xs">
                                <div className="font-semibold text-gray-900">
                                    {course.profiles?.full_name || 'Expert'}
                                </div>
                                <div className="text-[10px] text-slate-500">Instructor</div>
                            </div>
                        </div>
                        <div className="text-right">
                            <div className="text-2xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-[#1F2A6B] to-[#14B8A6]">
                                â‚¹{course.price}
                            </div>
                            <div className="text-[10px] text-slate-500">One-time payment</div>
                        </div>
                    </div>

                    {/* Hover Effect - Enrollment CTA */}
                    <div className="absolute inset-x-0 bottom-0 h-0 group-hover:h-16 bg-gradient-to-r from-[#1F2A6B] to-[#14B8A6] transition-all duration-300 flex items-center justify-center overflow-hidden">
                        <div className="flex items-center gap-2 text-white font-bold opacity-0 group-hover:opacity-100 transition-opacity duration-300">
                            <TrendingUp className="w-5 h-5" />
                            <span>Enroll Now</span>
                        </div>
                    </div>
                </div>
            </motion.div>
        </Link>
    );
};
